        padding: 0 0 0.6rem;
        >div:nth-child(2n+1){
          padding: 0.6rem 0.3rem 0 0.6rem;
        }
        >div:nth-child(2n){
          padding: 0.6rem 0.6rem 0 0.3rem;
        }
        .top-col-flex{
          width: 50%;
          box-sizing: border-box;
          .top-col-model{
            background: #4b4b4b;
            position: relative;
            border-radius: 0.2rem;
            overflow: hidden;
            .top-col-u{
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-align: center;
              -ms-flex-align: center;
              align-items: center;
              box-sizing: border-box;
              padding: 0.4rem;
              img{
                width: 1.5rem;
                height: 1.5rem;
                -o-object-fit: cover;
                object-fit: cover;
              }
              span{
                font-weight: 600;
                font-size: 1rem;
                margin: 0 0 0 0.4rem;
              }
            }
            .top-col-c{
              padding: 0.4rem;
              font-size: 0.7rem;
            }
            .top-col-b{
              display: flex;
              align-items: center;
              box-sizing: border-box;
              padding: 0.4rem;
              a{
                svg{
                  display: block;
                  width: 1rem;
                  height: 1rem;
                }
                img{
                  width: 1rem;
                  height: 1rem;
                  display: block;
                }
              }
            }
            .top-col-i{
              position: absolute;
              top: 0;
              right: 0;
              width: 2.5rem;
              height: 2.5rem;
            }
          }
        }
      }
      .top-model-list{
        padding: 0.6rem;
        .top-model-sw{
          width: 8rem;
          margin: 0 0 0.6rem 0;
        }
        .coupon-1{
          border-radius: 0.2rem 0.2rem 0 0;
          width: 8rem;
          background: radial-gradient(circle at 4rem 0rem, transparent 0.5rem, #e613f5 0) top;
          position: relative;
          &::after {
            content: "";
            height: .5rem;
            position: absolute;
            display: block;
            z-index: 9;
            background-size: 1rem 1rem;
            background-repeat: repeat-x,repeat-x;
            bottom: -.36rem;
            left: 0;
            right: 0;
            background-image: linear-gradient(-45deg,#e613f5 25%,transparent 25%,transparent),linear-gradient(-135deg,#e613f5 25%,transparent 25%,transparent),linear-gradient(-45deg,transparent 75%,#e613f5 75%),linear-gradient(-135deg,transparent 75%,#e613f5 75%);
          }
          /*filter: drop-shadow(3px 3px 3px rgba(255,255,255,.3));*/
          /*background-image: radial-gradient(shape size at position, start-color, …, last-color);*/
          /*（1）、shape 确定圆的类型:*/
          /* ellipse (默认): 指定椭圆形的径向渐变。*/
          /* circle ：指定圆形的径向渐变*/
          /*（3）、position 定义渐变的位置。可能值：*/
          /*center（默认）：设置中间为径向渐变圆心的纵坐标值。*/
          /*top：设置顶部为径向渐变圆心的纵坐标值。*/
          /*bottom：设置底部为径向渐变圆心的纵坐标值。*/
          /*圆形的位置也可修改，只需要改“circle at...”后面这个两个数值即可，上，右，下的数值分别是：50px,0、100px,50px、50px,100px,0px 50px。*/
